body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #567D46;
}

.game {
  padding: 20px 0;
  width: 450px;
  text-align: center;
  display: -webkit-flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
}

.main-area {
  letter-spacing: 0.2rem;
  color: white;
  display: -webkit-flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  height: 80vh;
}

.game-title {
  font-size: 2rem;
  width: 100%;
  color: white;
  text-align: center;
}

.welcome-message {
  font-size: 1.5rem;
  width: 100%;
  color: white;
  text-align: center;
}

.game-login {
  text-align: center;
}

.login-button {
  font-size: 1rem;
  color: white;
  border: 2px solid #FCB942;
  background: none;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Amaranth', sans-serif;
  letter-spacing: 0.13rem;
  margin: 5px;
  outline: 0;
}

.game-hole-area {
  margin: 2px;
  overflow: hidden;
  width: 120px;
  height: 120px;
}

.hole-location {
  background-image: url(/WhackAMoleGame/static/media/brown-hole.d6c13862.png);
  background-size: 95%;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
  position: relative;
  bottom: 25%;
  pointer-events: none;
}

.game-start-button {
  font-size: 1rem;
  padding: 0.6rem 0.6rem 0.7rem 0.6rem;
  color: white;
  border: 2px solid #FCB942;
  background: none;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Amaranth', sans-serif;
  letter-spacing: 0.13rem;
}

.game-board {
  width: 100%;
  height: 100%;
  text-align: center;
  display: -webkit-flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.game-mole {
  background-image: url(/WhackAMoleGame/static/media/mole.b01fcf84.png);
  background-repeat: no-repeat;
  background-size: 80px;
  background-position: center;
  height: 100%;
  cursor: pointer;
  -webkit-transform: translate(0, 60%);
          transform: translate(0, 60%);
}

.game-over {
  align-items: center;
  text-align: center;
}

.score-board {
  width: 100%;
  height: 100%;
  text-align: left;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.play-again-button {
  font-size: 1rem;
  padding: 0.6rem 0.6rem 0.7rem 0.6rem;
  color: white;
  border: 2px solid #FCB942;
  background: none;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Amaranth', sans-serif;
  letter-spacing: 0.13rem;
}

.level-button {
  font-size: 1rem;
  padding: 0.6rem 0.6rem 0.7rem 0.6rem;
  color: white;
  border: 2px solid #FCB942;
  background: none;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Amaranth', sans-serif;
  letter-spacing: 0.13rem;
  margin: 10px;
}

.error-message {
  color: purple;
  /* text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white; */
}
